html {
  font-family: 'Roboto', sans-serif;
  color: #2d2d2d;
  font-size: 18px;
  line-height: 1.65;
  font-weight: 300;
}
html,
body {
  overflow-x: hidden;
}
.lines {
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAABJMAAAABCAQAAADAmydUAAAAHklEQVR4Ae3BAQ0AAAzDoDqYf7fPfQC1gLeAt4C3AxuqAD3B4Pz6AAAAAElFTkSuQmCC") calc(50% - 1px) 0 repeat-y;
}
h2 {
  font-size: 36px;
  line-height: 1.35;
  text-align: center;
  font-weight: 500;
  margin-bottom: 64px;
}
h2 span {
  color: #e53641;
}
b {
  font-weight: bold;
}
.c {
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1190px;
  width: 100%;
}
@media (max-width: 1219px) {
  .c {
    max-width: 768px;
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .c {
    max-width: 100%;
  }
}
.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  will-change: filter;
}
.page.mfp-body-open {
  filter: blur(10px);
  overflow: hidden;
}
.lazy {
  visibility: hidden;
  opacity: 0;
}
.lazy.lazy--visible {
  visibility: visible;
  opacity: 1;
}
img[data-src],
img[data-srcset] {
  min-height: 1px;
}
.hidden {
  display: none;
}
#sprites {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  left: 0;
}
.zoom {
  cursor: pointer;
}
.zoom:after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  transition: all 0.2s ease-in;
  left: 50%;
  top: 50%;
  margin-left: -45px;
  margin-top: -45px;
  width: 90px;
  height: 90px;
  background: rgba(226,29,43,0.45) url("data:image/png;base64,") 50% 50% no-repeat;
  border-radius: 50%;
  opacity: 0;
}
.zoom:hover:after {
  opacity: 1;
}
.paroller {
  will-change: transform;
  transition: transform 0.1s ease-out !important;
}
@media (pointer: coarse) {
  .paroller {
    transition: none !important;
  }
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #e11b2a;
  padding: 0 40px;
  position: relative;
  font-weight: bold;
  height: 58px;
  cursor: pointer;
  border-radius: 29px;
  color: #fff;
  transition: all 0.1s ease-in;
}
.btn svg {
  display: block;
  transition: all 0.1s ease-in;
}
.btn:focus {
  outline: 0;
}
.btn:hover,
.btn--active {
  background: #bf1724;
}
.btn:hover svg,
.btn--active svg {
  fill: #fff;
}
.btn--sm {
  height: 50px;
  padding: 0 58px;
  border-radius: 25px;
}
.btn--round {
  width: 52px;
  height: 52px;
  padding: 0;
  align-items: center;
  justify-content: center;
}
.header {
  background: url("modules/header/header.jpg") 50% 0 no-repeat;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 134px;
  line-height: 1.35;
}
.header__left {
  display: flex;
  align-items: center;
}
.header__logo {
  width: 58px;
  height: 58px;
}
.header__title {
  width: 134px;
  height: 21px;
  margin-left: 16px;
  fill: #1d1d1b;
}
.header__label {
  font-size: 16px;
}
.header__value {
  font-weight: 500;
}
.header__contacts {
  position: relative;
}
.header__contacts-icon {
  position: absolute;
  fill: #e21d2b;
  width: 12px;
  height: 12px;
  right: 100%;
  margin-right: 10px;
  top: 6px;
}
.header__working {
  position: relative;
}
.header__working-icon {
  position: absolute;
  fill: #e21d2b;
  width: 16px;
  height: 16px;
  right: 100%;
  margin-right: 10px;
  top: 4px;
}
.header__menu {
  border: 1px solid rgba(27,28,32,0.1);
  border-left: 0;
  border-right: 0;
}
.header__menu ul {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}
.header__menu a {
  position: relative;
  padding: 20px 0;
  font-size: 14px;
  font-weight: bold;
  color: #454545;
  display: block;
  height: 100%;
}
.header__menu a:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0;
  background: #e21d2b;
  transition: all 0.1s ease-in;
}
.header__menu a:hover:after,
.header__menu a.current-menu-item:after {
  height: 3px;
}
.welcome {
  background: url("modules/welcome/welcome.jpg") 50% 0 no-repeat;
  padding: 61px 0;
  height: 666px;
}
.welcome__top {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 9px;
}
.welcome__title {
  display: block;
  width: 614px;
  height: 88px;
  margin-bottom: 34px;
  fill: #1d1d1b;
}
.welcome__sub {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px;
}
.welcome__list li {
  margin-bottom: 18px;
  padding-left: 16px;
}
.welcome__list a {
  font-weight: bold;
}
.welcome__list a:before {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  background: #e21d2b;
  border-radius: 50%;
  margin-top: 12px;
  margin-left: -16px;
}
.welcome__list a:hover span:after {
  width: 100%;
}
.welcome__list a:hover .welcome__dot:before {
  width: 38px;
  height: 38px;
}
.welcome__list a:hover .welcome__dot:after {
  width: 14px;
  height: 14px;
}
.welcome__list span {
  display: inline-block;
  position: relative;
}
.welcome__list span:after {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 3px;
  background: #e21d2b;
  transition: all 0.1s ease-in;
}
.welcome__list sub {
  font-size: 16px;
  font-weight: 500;
  bottom: 0;
}
.welcome__dot {
  position: absolute;
  width: 38px;
  height: 38px;
  left: 50%;
}
.welcome__dot:before,
.welcome__dot:after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.1s ease-in;
}
.welcome__dot:before {
  width: 14px;
  height: 14px;
  background: #e43741;
}
.welcome__dot:after {
  width: 0;
  height: 0;
  background: #fff;
}
.welcome__dot--1 {
  margin-left: -187px;
  top: 520px;
}
.welcome__dot--2 {
  margin-left: 53px;
  top: 629px;
}
.welcome__dot--3 {
  margin-left: 291px;
  top: 635px;
}
